.App {
  display: flex;
  flex-direction: column;
  padding: 5px;
  border-radius: 10px;
  margin: 10px;
}

.codeenv {
  display: flex;
  flex-direction: row;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
}

.question {
  resize: horizontal;
  overflow: auto;
  padding: 10px;
  max-width: 60%;
  min-width: 20%;
  height: max-content;
  border: 1px solid black;
  border-radius: 8px;
}

.statement {
  text-wrap: wrap;
  background: black;
}
